html, body {
  max-width: 100%;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Inter';
}

:root {
  --primary-color: #ffb238;
  --secondary-color: #121428;
}

::-webkit-scrollbar {
  width: 7px;
  background-color: rgb(0, 0, 0);
}

::-webkit-scrollbar-thumb {
  background-color: #ffb238;
  background-color: var(--primary-color);
}

body {
  background-color: #000;
}

.home,
.events,
.products,
.faqs {
  padding: 20px 100px 20px 100px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.events {
  padding: 4rem;
  background-color: #000;
  color: #fff;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

h6 {
  font-size: 40px;
  margin-bottom: 10px;
}

h8 {
  font-size: 20px;
}

.navbar {
  color: black;
  /* border-bottom: 3px solid #026857; */
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  top: 0;
  transition: 20ms;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar.active {
  background: black;
  color: white;
}

table, th, td {
  border: 1px solid black;
  border-collapse: collapse;
  
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

@media (max-width: 600px) {

  .events {
    padding: 15px;
  }

}
.whiteClassName {
    color: white;
    transition: 0s all;
  }

  .navLogo {
    color: var(--primary-color);
    font-family: Inter;
    cursor: pointer;
    font-size: 37px;
    text-shadow: 0 0 5px var(--primary-color);
    margin-left: auto;
  }

  .logo-title {
    cursor: pointer;
    display: inline-block;
    position: relative;
    font-size: 25px;
    
    font-weight: 200;
  }

  .logo-title-moved {
    cursor: pointer;
    transition-delay: 37ms;
    font-weight: 200;
  }

  .icon-logo {
    display: none;
  }

  .dropdown {
    position: relative;
    display: inline-block;
    color: white;
  }

  .dropbtn {
    display: flex;
    align-items: center;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background: var(--secondary-color);
    border-radius: 30px;
    min-width: 180px;
    z-index: 999;
    top: 100%;
    margin-top: -5px;
  }

  .dropdown-content a {
    color: var(--primary-color);
    font-size: 20px;
    font-weight: 400;
  }

  .dropdown-content a:hover {
    text-decoration: underline;
  }

  .dropdown:hover .dropdown-content, .dropdown-content:hover {
    display: block;
  }
  
  @media (max-width: 600px) {
    .logo-title {
      display: none;
      width: 0%;
    }
    
    .logo-title-moved {
        margin: 0;
    }

    .icon-logo {
        display: inline-block;
        position: relative;
        display: initial;
        font-size: 25px;
        margin-left: 20px;
        font-weight: 200;
    }
      
  }
  
  
body {
    color: black;
}

.bxsci {
    font-size: 100px;
    font-weight: 700;
    font-family: 'Inter';
    color: white;
    text-shadow: 0 0 10px #fff;
}

.scioly {
    font-size: 100px;
    margin-bottom: 20px;
    font-weight: 700;
    font-family: 'Inter';
    color: white;
}

.desc {
    font-size: 40px;
    font-weight: 600;
    font-family: 'Inter';
    color: white;
}

.scioly:hover, .bxsci:hover, .desc:hover {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.rotated {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}

.show-answer {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
}

.blob-1 {
    height: 45%;
    position: absolute;
    left: -14%;
}

.blob-4 {
    height: 500px;
    position: absolute;
    left: -14%;
    top: 1000px;
}

.blob-2 {
    height: 500px;
    position: absolute;
    right: -13%;
    bottom: 10%;
}

.blob-3 {
    height: 500px;
    position: absolute;
    right: -12%;
    top: 1200px;
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
}

.page {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000;
}

.about-text {
    font-weight: 400;
    font-size: 20px;
}

.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 450px;
    border-radius: 30px;
  }

.about-title {
    font-size: 35px;
    margin-bottom: 50px;
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--primary-color);
}

.item-icon {
   font-size: 55px; 
   color: var(--primary-color);
}

.number {
    font-size: 50px;
    font-weight: 600;
}

.descript {
    font-size: 18px;
    margin-top: 70px;
    text-align: center;
}

.background {
    width: 100vw;
    position: absolute;  
}

.logo {
    width: 300px;
    height: 300px;
    margin: 3rem 0;
}


@media (max-width: 600px) {
    .page {
        padding: 2rem;
    }

    .bxsci{
        font-size: 35px;
    }
    .scioly{
        font-size: 35px;
    }
    .desc{
        font-size: 25px;
    }
    .rotated {
        font-size: 20px;
    }
    .show-answer {
        font-size: 20px;
    }

    .blob-1, .blob-2, .blob-3, .blob-4 {
        display: none;
    }

    .about-text {
        font-size: 16px;
        text-align: left;
    }

}
